    white-space: pre-wrap !important;
}


/* UPCOMING CHALLENGES */
.upcomingChallenges {
    text-align: center;
    overflow: auto !important;
    height: 400px;
}
.upcomingChallenges .card {
    border: none;
}
.upcomingChallenges .challengeCard {
    /* float: left; */
    display: inline-block;
    margin: 0 auto ;
    margin-bottom: 5%;
    margin: 1%;
    width: 40%;
    border: solid 2px gold;
    border-radius: 1%;
}
.challengeCard .card-title {
    font-weight: bolder;
    font-size: 16px;
}
.challengeCard .card-subtitle {
    font-size: 12px;
    color: red;
    margin: 5px;
}
.challengeCard .card-body {
    color: gold;
    background-color: #209CEE;
}
.challengeCard .card-link {
    color: gold;
    padding: 2%;
    font-weight: bolder;
}
.challengeCard .card-link:hover {
    background-color: gold !important;
    color: #209CEE !important;
    cursor: pointer !important;
}
.challengeCard .signUpDate {
    color: white !important;
}
.challengeCard .rulesLink {
    color: white !important;
}
.challengeCard .signUp {
    color: lightgreen !important;
}
.challengeCard .challengeInfo {
    font-size: 14px;
}


